import React from 'react';
import { useNavigate } from 'react-router-dom';
import '../../css/help.css';
import backArrow from '../images/arrow_back.png';

const Help = () => {
    const navigate = useNavigate();
	const handleBackArrow = () => {
		navigate('/login')
	}
	return (
		<React.Fragment>
			<div className="helpcontainer">
				<nav className='helpNav'>
                	<div className="backButtonWrapper">
                        <div className="backButton" onClick={handleBackArrow}>
                            <img src={backArrow} alt="back" />
                    	</div>
					</div>
				</nav>
				<main className='helpMain'>
					<h1>Aide pour se connecter</h1>
					<p>
					Vous pouvez verifier ces quelques points si vous avez des problmes pour vous connecter: <br />
					- L'API se lance automatiquement dans le terminal que vous avez utilis pour excuter le script, si vous l'avez ferm il faut rexcuter le script ou lancer le fichier <span>api.py</span> dans <span>website/backend/</span> avec python. <br />
					- Si l'API ne se lance pas vous pouvez avoir un problme de librairie c.f. point n2<br />
					- Si l'API se lance mais que vous avez toujours une erreur, vrifiez si le fichier <span>model.keras</span> est bien dans le rpertoire <span>website/backend/models</span> si il n'y est pas vous avez peut-tre oubli d'excuter le notebook <span>6_CNN.ipynb</span> pour crer le dataset.<br />
					- Si l'API est bien lance vous pouvez essayer de crer votre propre dataset en excutant le notebook <span>dataset_binaire.ipynb</span> dans <span>dataset/</span> et en relanant <span>6_CNN.ipynb</span>. <br />
					<strong>Si aucun des points ci-dessus n'a t utile veuillez nous contacter par mail pour que l'on tente de vous aider.</strong><br />
					</p>
				</main>
			</div>
		</React.Fragment>
	)
}

export default Help;